.btn {
  margin-top: 2rem;
  padding: 0.6rem 2rem;
  color: var(--color-white);
  font-weight: 300;
  font-size: 1.2rem;
  background-color: var(--primary);
  border: none;
  border-radius: 20px;
  cursor: pointer;

  &-dark {
    background-color: var(--primary-dark);
  }
}

// dark mode
.app.dark {
  .btn {
    border: 1px solid var(--primary-light);

    &-dark {
      background-color: var(--color-dark);
    }
  }
}
